.friend_wrapper {
    display: grid;
    grid-template-rows: 7vh 7vh auto;
    grid-auto-rows: minmax(min-content, max-content);
    height: 55vh;
    border-radius: 5px;
    background-color: whitesmoke;
}

.background {
    grid-row: 1/3;
    overflow: hidden;
    width: 100%;
    grid-column-start: 1;
}

.background img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    box-sizing: border-box;

}

.friend_info_wrapper {
    display: grid;
    grid-template-rows: repeat(2, 7vh) 10vh 1fr;
    grid-row-start: 2;
    grid-column-start: 1;
    justify-items: center;
    align-items: center;
    text-align: center;

}

.ava {
    grid-row: 1/3;
    width: 13vh;
    height: 13vh;


}

.ava img {

    display: flex;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    border: 4px solid whitesmoke;
    object-fit: cover;
    background-color: #3F4257;
}

.info{
    grid-row: 3/4;
    grid-column-start: 1;
    font-size: 16px;
    align-self: center;

}

.name{
    font-weight: 600;
}

.location{
    font-size: 13px;
    color: lightslategray;
}

.about_wrapper{
    display: grid;
    grid-template-rows: repeat(2, 1fr);

}

.media{
    display: grid;
    /*grid-template-rows: repeat(2, 1fr);*/


}

.count{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2vw;
    padding-top: 10px;
    font-size: 14px;
    font-weight: 600;
}

.stat_name{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2vw;
    font-size: 13px;
    font-weight: 500;
    color: lightslategray;

}

.connect{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2vw;
    align-items: center;
    padding-top: 2vw;

}

.profile_page{
    width: 20px;
    height: 20px;
    justify-self: end;
    background-color: #7C5AC2;
    padding: 10px;
    border-radius: 100%;
}

.profile_page img{
    display: flex;
    width: 100%;
    height: 100%;

}

.dialog{
    width: 20px;
    height: 20px;
    justify-self: start;
    padding: 10px;
    border-radius: 100%;
    background-color: #38A9FF;
}

.dialog img{
    display: flex;
    width: 100%;
    height: 100%;
}